import React, { Component, Fragment, memo } from 'react';
import { connect } from "react-redux";
import queryString from 'query-string';
import { Tab, Nav } from 'react-bootstrap';
import moment from 'moment';
import { Link } from 'react-router-dom';
import { baseUrl, ImagePlaceholder } from "../../../../Assets/AssetsHelper";
import { geturl, SafariStatus } from "../../../../utils"
import { getAllNotification } from '../../../../actions/notificationAction';


export const getNotifyLoad = (notifications) => {
    var data = {}
    if (notifications && notifications.length > 0) {
        notifications.map((item) => {
            if (data[moment(new Date(item.message.time * 1000)).fromNow()]) {
                if (!data[moment(new Date(item.message.time * 1000)).fromNow()].includes(item.id)) {
                    data[moment(new Date(item.message.time * 1000)).fromNow()].push(item)
                }
            }
            else data[moment(new Date(item.message.time * 1000)).fromNow()] = [item]
        })
        return data
    }
    else return data
}
class Notifications extends Component {
    constructor(props) {
        super(props)
        this.state = {
            notifications: getNotifyLoad(props.notificationList),
        }
    }
    updateStatus = (Item) => {
        var self = this;
        if (!Item.status) {
            // notificationUpdateStatus(Item.id, 1).then((response) => {
            //     self.props.getLoadNotify()
            // }).catch((ex) => { { } })
        }
        this.props.onNotificationToggle()
        this.onRequest()
    }

    getUnreadNotifications = (notifications) => {
        if (notifications && Object.keys(notifications).length > 0) {
            const unreadList = Object.entries(notifications).reduce((acc, [key, notify]) => {
                const filtered = notify.filter(item => item.status == 0)
                if (filtered.length > 0) acc.push([key, filtered])
                return acc;
            }, [])
            return Object.fromEntries(unreadList)
        }
        else return {}
    }
    onRequest = () => {
        const parsed = queryString.parse(window.location.search);
        if ((parsed && Object.keys(parsed).length > 0) || String(window.location.href).includes("assets")) {
            const channel = SafariStatus ? window : new BroadcastChannel('connect');
            if (SafariStatus) window.opener.postMessage(JSON.stringify({ type: 'assets-redirect' })); else channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
        }
    }
    componentWillReceiveProps(nextProps) {
        const { notificationList } = nextProps
        if (JSON.stringify(notificationList) != JSON.stringify(this.state.notifications)) this.setState({ notifications: getNotifyLoad(notificationList) })
    }

    onClickNotification = () => {

    }


    render() {
        const { onNotificationToggle } = this.props
        const { notifications } = this.state;
        const unreadNotifications = this.getUnreadNotifications(notifications)
        return <div className="w-100">
            <Tab.Container id="notifications_tab_panel" defaultActiveKey="all-notifications">
                <div className="f-between px-3 border-bottom">
                    <Nav variant="underline" className="nav">
                        <Nav.Item className="">
                            <Nav.Link className="me-3 py-3 px-0 fw-medium" eventKey="all-notifications">All</Nav.Link>
                        </Nav.Item>
                        <Nav.Item className="">
                            <Nav.Link className="px-0 py-3 fw-medium" eventKey="unread-notifications">Unread</Nav.Link>
                        </Nav.Item>
                    </Nav>
                    <div>
                    {Object.keys(unreadNotifications).length > 0 ?   <button type="button" className="btn btn-link btn-sm px-0 h-auto fw-bold text-decoration-none btn-active-color-primary" onClick={() =>this.props.getAllNotification('markread','')}>Mark all as read</button>:''}
                    </div>
                </div>
                <div className='overflow-auto' style={{height:380}}>
                    {notifications && Object.keys(notifications).length > 0 ? <Tab.Content>
                        <Tab.Pane eventKey="all-notifications" className="pb-3 min-h-100">
                            {
                                notifications && Object.keys(notifications).map((iNot, index) => {
                                    return <Fragment key={index}><div className="f-between px-3 py-1" >
                                        <span className="fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 d-none">{(notifications[iNot] && notifications[iNot].length > 0) ? notifications[iNot].length : 0}</span>
                                    </div>
                                        {notifications[iNot] && notifications[iNot].length > 0 ? <div className="">

                                            {
                                                notifications[iNot].map((Item) => {
                                                    return <Link to={geturl(Item.url)} key={Item.id} className={`px-3 d-flex align-items-start border-bottom notification-item${Item.status == 0 ? ` bg-gray-100` : ``}`}  onClick={() =>{ if(Item.status == 0) this.props.getAllNotification('all',Item.id); onNotificationToggle()}}>
                                                        <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 p-2">
                                                            <img alt="Pic" src={Item.message.photo} height="40" width="40" onError={event => event.target.src = ImagePlaceholder} />
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column flex-grow-1">
                                                            <div className="d-flex algin-items-center justify-content-between w-100">
                                                            {Item.message.subject != '' ? <span className="text-hover-primary text-gray-800 fw-bold me-2">{Item.message.subject}</span>: null}
                                                                <span className="text-gray-500 fs-9">{moment(new Date(Item.message.time * 1000)).format("hh:mma")}</span>
                                                            </div>
                                                            <div className="d-flex algin-items-start">
                                                                <span className="text-gray-600 fs-9">
                                                                    {Item.message.text}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </Link>

                                                })
                                            }

                                        </div> : index == 0 ?
                                            <div className="f-center" style={{ height: 350 }}>
                                                <p className="mb-0 text-muted">No Notifications</p>
                                            </div>
                                            : null}
                                    </Fragment>

                                })
                            }
                        </Tab.Pane>
                        <Tab.Pane eventKey="unread-notifications" className="pb-3 min-h-100">
                            {unreadNotifications && Object.keys(unreadNotifications).length > 0 ? Object.keys(unreadNotifications).map((iNot, index) => {
                                return <Fragment key={index}>
                                    <div className="f-between px-3 py-1">
                                        <span className="fw-bold">{iNot}</span>
                                        <span className="bg-primary badge rounded-5 min-w-30 d-none">{unreadNotifications[iNot] && unreadNotifications[iNot].length > 0 && unreadNotifications[iNot].filter((a) => a.status == 0).length > 0 ? unreadNotifications[iNot].filter((a) => a.status == 0).length : 0}</span>
                                    </div>
                                    {unreadNotifications[iNot] && unreadNotifications[iNot].length > 0 ?
                                        <div className="">
                                            {unreadNotifications[iNot].map((Item, index) => {
                                                return Item.status == 0 ?
                                                    <Link to={Item.getNotificationUrl ? Item.getNotificationUrl : `#`} key={index} className="px-3 d-flex align-items-start border-bottom notification-item text-decoration-none" onClick={() =>{ if(Item.status ==0) this.props.getAllNotification('all',Item.id); onNotificationToggle()}}>
                                                        <div className="symbol symbol-38 rounded-1 overflow-hidden me-2 p-2">
                                                            <img alt="Pic" src={Item.message.photo} height="40" width="40" onError={event => event.target.src = ImagePlaceholder} />
                                                        </div>
                                                        <div className="d-flex align-items-start flex-column flex-grow-1">
                                                            <div className="d-flex algin-items-center justify-content-between w-100">
                                                                {Item.message.subject != '' ? <span className="text-hover-primary fw-bold text-decoration-none me-2">{Item.message.subject}</span> : null}
                                                                <span className="text-gray-500 fs-9">{moment(new Date(Item.message.time * 1000)).format("hh:mma")}</span>
                                                            </div>
                                                            <div className="d-flex algin-items-start">
                                                                <span className="text-gray-600 fs-9">
                                                                    {Item.message.text}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </Link> : null

                                            })}
                                        </div>
                                        : null}
                                </Fragment>
                            }) :
                                <div className="f-center" style={{ height: 350 }}>
                                    <p className="mb-0 text-gray-500 fs-8">No Unread Notifications</p>
                                </div>
                            }
                        </Tab.Pane>
                    </Tab.Content> : <div className="f-center" style={{ height: 150 }}>
                        <p className="mb-0 text-gray-500 fs-8">No Notifications</p>
                    </div>}
                </div>
            </Tab.Container>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        notificationList: state.notification.notificationList
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getAllNotification: (type,id) => dispatch(getAllNotification(type,id)),
    }
  }
export default connect(mapStateToProps, mapDispatchToProps)(memo(Notifications));