
import { DELETE_NOTIFICATION, GET_ALL_NOTIFICATION, GET_LATEST_NOTIFICATION } from "../reducer/keys";
import { TOAST_SUCCESS } from "../utils";
import { DELETE_NOTIFICATION_API, GET_ALL_NOTIFICATION_API } from "./api";
import HttpClient from "./http-client";

export const getAllNotification = (type, id) => {
  const postbody = { type , id, page: 0, perPage: 20 }
  
  return async (dispatch) => {
    await new HttpClient() .post(GET_ALL_NOTIFICATION_API, postbody) .then((response) => {
        const { responseData, responseCode } = response.data;
        if (responseCode === 200) {
            const { notifications, count } = responseData;
            dispatch({ type: GET_ALL_NOTIFICATION, payload: { notifications, count } });
        }
      })
      .catch((ex) => { });
  };
};

export const deleteNotification = (id) => {
  return async (dispatch) => {
    await new HttpClient()
      .delete(`${DELETE_NOTIFICATION_API}/${id}`)
      .then((response) => {
        const { response_message, responseCode } = response.data;

        if (responseCode === 200) {
          dispatch({
            type: DELETE_NOTIFICATION,
            payload: id,
          });
          TOAST_SUCCESS(response_message, 2000);
        }
      })
      .catch((ex) => {

      });
  };
}